// Specification: Hook to manage job applications data lifecycle.
// Manages loading/error states, exposes CRUD helpers, and applies simple frontend filters.

import { useCallback, useEffect, useMemo, useState } from 'react';
import {
  CreateJobApplicationPayload,
  createJobApplication,
  getJobApplications,
  updateJobApplicationStatus,
  deleteJobApplication,
} from '../services/jobApplicationsApi';
import { JobApplication, JobStatus } from '../types/jobApplication';

type DateRangePreset = 'all' | '7d' | '30d';

export function useJobApplications() {
  const [applications, setApplications] = useState<JobApplication[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [statusFilter, setStatusFilter] = useState<JobStatus | 'all'>('all');
  const [dateRange, setDateRange] = useState<DateRangePreset>('all');

  const load = useCallback(async () => {
    setLoading(true);
    setError(null);
    setSuccess(null);
    try {
      const data = await getJobApplications();
      setApplications(data);
    } catch (err) {
      const message = err instanceof Error ? err.message : 'No pudimos cargar tus postulaciones. Probá de nuevo.';
      setError(message);
    } finally {
      setLoading(false);
    }
  }, []);

  const create = useCallback(async (payload: CreateJobApplicationPayload) => {
    setLoading(true);
    setError(null);
    setSuccess(null);
    try {
      await createJobApplication(payload);
      setSuccess('Postulación registrada. ¡Seguimos midiendo tu progreso!');
      await load();
    } catch (err) {
      const message = err instanceof Error ? err.message : 'No pudimos guardar la postulación. Intentá otra vez.';
      setError(message);
      throw err;
    } finally {
      setLoading(false);
    }
  }, [load]);

  const updateStatus = useCallback(
    async (id: string, status: JobStatus) => {
      setLoading(true);
      setError(null);
      setSuccess(null);
      try {
        await updateJobApplicationStatus(id, status);
        setSuccess('Estado actualizado. Progreso al día.');
        await load();
      } catch (err) {
        const message = err instanceof Error ? err.message : 'No pudimos actualizar el estado. Reintenta en unos segundos.';
        setError(message);
        throw err;
      } finally {
        setLoading(false);
      }
    },
    [load],
  );

  const removeApplication = useCallback(
    async (id: string) => {
      setLoading(true);
      setError(null);
      setSuccess(null);
      try {
        await deleteJobApplication(id);
        setSuccess('Postulacion eliminada.');
        await load();
      } catch (err) {
        const message = err instanceof Error ? err.message : 'No pudimos eliminar la postulacion. Intenta de nuevo.';
        setError(message);
        throw err;
      } finally {
        setLoading(false);
      }
    },
    [load],
  );

  useEffect(() => {
    load();
  }, [load]);

  const filteredApplications = useMemo(() => {
    const now = new Date();
    const dateThreshold = (() => {
      if (dateRange === '7d') {
        return new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000);
      }
      if (dateRange === '30d') {
        return new Date(now.getTime() - 30 * 24 * 60 * 60 * 1000);
      }
      return null;
    })();

    return applications.filter((app) => {
      const matchesStatus = statusFilter === 'all' || app.status === statusFilter;
      const matchesDate =
        !dateThreshold ||
        new Date(app.applicationDate).getTime() >= dateThreshold.getTime();
      return matchesStatus && matchesDate;
    });
  }, [applications, statusFilter, dateRange]);

  return {
    applications: filteredApplications,
    rawApplications: applications,
    loading,
    error,
    reload: load,
    create,
    updateStatus,
    remove: removeApplication,
    success,
    filters: {
      status: statusFilter,
      setStatus: setStatusFilter,
      dateRange,
      setDateRange,
    },
  };
}
