'use client';

// Specification: Main dashboard page assembling all sections.
// Composes header, metrics, creation form, and listing using the job applications hook.

import { FormularioPostulacion } from '../components/job-applications/FormularioPostulacion';
import { ListadoPostulaciones } from '../components/job-applications/ListadoPostulaciones';
import { MetricsSection } from '../components/job-applications/MetricsSection';
import { useJobApplications } from '../hooks/useJobApplications';

export default function Page() {
  const {
    applications,
    rawApplications,
    loading,
    error,
    create,
    updateStatus,
    remove,
    filters,
    reload,
    success,
  } = useJobApplications();

  return (
    <main className="min-h-screen bg-gradient-to-b from-slate-100 to-white">
      <div className="mx-auto max-w-6xl space-y-6 px-4 py-10">
        <header className="space-y-3 rounded-2xl border border-slate-200 bg-white/70 p-6 shadow-sm backdrop-blur">
          <p className="text-xs font-semibold uppercase tracking-wide text-slate-500">
            Progreso en tu busqueda
          </p>
          <h1 className="text-3xl font-bold text-slate-900">Dashboard de postulaciones</h1>
          <p className="text-sm text-slate-600">
            Registra tus aplicaciones, monitorea avances y celebra cada paso adelante. Aqui ves tu
            esfuerzo transformado en progreso concreto.
          </p>
        </header>

        <MetricsSection applications={rawApplications} />

        <FormularioPostulacion onSubmit={create} loading={loading} error={error} success={success} />

        <ListadoPostulaciones
          applications={applications}
          loading={loading}
          error={error}
          filters={filters}
          onChangeStatus={updateStatus}
          onDelete={remove}
          onRetry={reload}
          success={success}
        />
      </div>
    </main>
  );
}
