// Specification: Metrics section aggregating MetricCard components.
// Computes key metrics from job applications and renders them in a responsive grid.

import { JobApplication, JobStatus } from '../../types/jobApplication';
import { MetricCard } from './MetricCard';

type MetricsSectionProps = {
  applications: JobApplication[];
};

export function MetricsSection({ applications }: MetricsSectionProps) {
  const total = applications.length;

  const sevenDaysAgo = new Date(Date.now() - 7 * 24 * 60 * 60 * 1000);
  const last7Days = applications.filter(
    (app) => new Date(app.applicationDate).getTime() >= sevenDaysAgo.getTime(),
  ).length;

  const statusCounts = applications.reduce<Record<JobStatus, number>>(
    (acc, app) => {
      acc[app.status] = (acc[app.status] || 0) + 1;
      return acc;
    },
    {
      [JobStatus.ENVIADA]: 0,
      [JobStatus.EN_PROCESO]: 0,
      [JobStatus.ENTREVISTA]: 0,
      [JobStatus.RECHAZADA]: 0,
      [JobStatus.SIN_RESPUESTA]: 0,
    },
  );

  return (
    <div className="grid gap-4 md:grid-cols-2 xl:grid-cols-4">
      <MetricCard title="Total postulaciones" value={total} hint="Todo tu esfuerzo acumulado" />
      <MetricCard title="Últimos 7 días" value={last7Days} hint="Actividad reciente que suma" />
      <MetricCard
        title="Enviadas"
        value={statusCounts[JobStatus.ENVIADA]}
        hint="Pendientes de respuesta"
      />
      <MetricCard
        title="En proceso"
        value={statusCounts[JobStatus.EN_PROCESO]}
        hint="Seguimiento activo"
      />
      <MetricCard
        title="Entrevista"
        value={statusCounts[JobStatus.ENTREVISTA]}
        hint="Agendar y preparar"
      />
      <MetricCard
        title="Rechazadas"
        value={statusCounts[JobStatus.RECHAZADA]}
        hint="Aprendizajes recientes"
      />
      <MetricCard
        title="Sin respuesta"
        value={statusCounts[JobStatus.SIN_RESPUESTA]}
        hint="Considerar follow-up"
      />
    </div>
  );
}
