// Specification: HTTP client for job applications API.
// Provides typed helpers to call the backend endpoints with basic error handling.

import { JobApplication, JobStatus } from '../types/jobApplication';

const API_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3000';

type Filters = {
  status?: JobStatus;
  fromDate?: string;
  toDate?: string;
};

export type CreateJobApplicationPayload = {
  company: string;
  position: string;
  source: string;
  applicationDate: string;
  status: JobStatus;
  notes?: string;
  jobUrl?: string;
};

async function handleResponse<T>(response: Response): Promise<T> {
  if (!response.ok) {
    const message = await response.text();
    throw new Error(message || 'Error en la solicitud');
  }
  return response.json() as Promise<T>;
}

export async function getJobApplications(filters?: Filters): Promise<JobApplication[]> {
  const url = new URL('/job-applications', API_URL);
  if (filters?.status) url.searchParams.set('status', filters.status);
  if (filters?.fromDate) url.searchParams.set('fromDate', filters.fromDate);
  if (filters?.toDate) url.searchParams.set('toDate', filters.toDate);

  const res = await fetch(url.toString(), { cache: 'no-store' });
  return handleResponse<JobApplication[]>(res);
}

export async function createJobApplication(
  payload: CreateJobApplicationPayload,
): Promise<JobApplication> {
  const res = await fetch(`${API_URL}/job-applications`, {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(payload),
  });
  return handleResponse<JobApplication>(res);
}

export async function updateJobApplicationStatus(
  id: string,
  status: JobStatus,
): Promise<JobApplication> {
  const res = await fetch(`${API_URL}/job-applications/${id}`, {
    method: 'PATCH',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({ status }),
  });
  return handleResponse<JobApplication>(res);
}

export async function deleteJobApplication(id: string): Promise<JobApplication> {
  const res = await fetch(`${API_URL}/job-applications/${id}`, {
    method: 'DELETE',
  });
  return handleResponse<JobApplication>(res);
}
